<template>
	<v-content>
		<v-container fluid fill-height>
			<v-layout align-center justify-center>
				<v-flex xs12 sm8 md4>
					<v-card class="elevation-12">
						<v-toolbar dark color="primary">
							<v-toolbar-title>{{ formName }}</v-toolbar-title>
							<v-spacer></v-spacer>
						</v-toolbar>
						<v-card-text>
							<v-alert
									:value="getError.message"
									type="warning"
							>
								{{ getError.message }}
							</v-alert>
							<v-form
									v-model="valid"
							>
								<v-text-field
										prepend-icon="mail"
										name="email"
										label="E-mail"
										type="email"
										required

										v-model="email"
										:error="getError.code === 'auth/invalid-email'"
										:rules="[rules.required, rules.email]"
								></v-text-field>
								<v-text-field
										id="password"
										prepend-icon="lock"
										name="password" label="Пароль"
										type="password" required

										v-model="password"
										:error="getError.code === 'auth/weak-password'"
										:rules="[rules.required, rules.min]"
								></v-text-field>
							</v-form>
						</v-card-text>
						<v-card-actions>
							<v-spacer></v-spacer>
							<v-btn
									color="primary"
									v-if="isSignInBtn"
									to="/sign-up"
							>Зарегистрироваться</v-btn>
							<v-btn
									color="primary"
									v-if="isSignUpBtn"
									to="/sign-in"
							>Уже есть аккаунт</v-btn>
							<v-btn
									color="success"
									:disabled="getProcessing || !valid"
									@click.prevent="sendForm(formAction)"
							>{{ formButton }}</v-btn>
						</v-card-actions>
					</v-card>
				</v-flex>
			</v-layout>
		</v-container>
	</v-content>
</template>

<script>
	import {mapGetters, mapActions} from 'vuex'

	export default {
		name: 'AuthForm',
		data: () => ({
			email: '',
			password: '',
			valid: false,
			rules: {
				required: value => !!value || 'Это поле обязательное',
				min: v => v.length >= 6 || 'Пароль должен быть не меньше 6 символов',
				email: value => {
					const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
					return pattern.test(value) || 'Пожалуйста введите корректный e-mail'
				}
			}
		}),
		props: {
			formName: {
				default: 'Регистрация',
				type: String
			},
			formButton: {
				default: 'Зарегистрироваться',
				type: String
			},
			formAction: {
				default: 'registrationUser',
				type: String
			}
		},
		computed: {
			...mapGetters([
				'getError',
				'getProcessing',
				'getUserAuth'
			]),
			isSignInBtn() {
				return this.$router.currentRoute.path === '/sign-in'
			},
			isSignUpBtn() {
				return this.$router.currentRoute.path === '/sign-up'
			}
		},
		methods: {
			...mapActions([
				'registrationUser',
				'signIn'
			]),
			sendForm(formAction) {
				this.$store.dispatch(formAction, {email: this.email, password: this.password})
			}
		},
	}
</script>

